












import nowPlay from '@/components/nowPlay.vue';
export default {
	data() {
		return {
			personalizedList:''
		};
	},
	components: {
		nowPlay
	},
	onLoad() {
		this.getPersonalizedList()
	},
	methods: {
		getPersonalizedList() {
			
			//获取推荐歌单
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.personalized,
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.personalizedList = res.data.result;
					} else {
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		openPersonalizedDetail(id){
			
			//跳转歌单详情
			var id = id;
			uni.navigateTo({
				url: '../getPlaylistDetail/getPlaylistDetail?id='+id
			});
		}
	}
}
